<template>
  <div>
    <div class="form-container">
      <input type="text" placeholder="last name" v-model="lname">
      <br>
      <input type="text" placeholder="f name" v-model="fname">
      <br>
      <input type="text" placeholder="gender" v-model="gender">
      <input type="number" placeholder="grade" v-model="grade">
      <button type="button" @click="createStudent">Add</button>
    </div>
    <input type="text" class="search" placeholder="Search for last Name" v-model="searchQuery">
    <table>
      <thead>
        <tr>
          <th>Last Name</th>
          <th>First Name</th>
          <th>Gender</th>
          <th>Grade</th>
          <th>action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="student in filteredStudents" :key="student.id">
          <td>{{ student.lname }}</td>
          <td>{{ student.fname }}</td>
          <td>{{ student.gender }}</td>
          <td :class="getGradeClass(student.grade)">{{ student.grade }}</td>
          <td>
            <button type="button" @click="deleteStudent(student.id)">remove</button>
          </td>

        </tr>
      </tbody>
    </table>
  </div>
</template>

<script setup>
import { db } from "./firebase/index.js";
import { ref, onMounted,computed } from 'vue';
import { addDoc,collection, onSnapshot,getDocs,deleteDoc,doc } from 'firebase/firestore';

const lname = ref('');
const fname = ref('');
const gender = ref('');
const sex = ref('');
const grade = ref('');
const collectionRef = collection(db,"Studentdb");
const searchQuery =ref('')

const students = ref([]);

const fetchInitialData = async () => {
  try {
    console.log('Fetching initial data...');

    const querySnapshot = await getDocs(collectionRef);
    students.value =querySnapshot.docs.map((doc) => {return{
      ...doc.data(),
      id: doc.id,
    }
    });
    console.log('Students:', students.value);
  } catch (error) {
    console.error('Error fetching initial data:', error.message);
  }
};
const getGradeClass = (grade) => {
  return grade < 75 ? 'low-grade' : 'high-grade';
};


onMounted(() => {
  fetchInitialData
  // Fetch initial data
  onSnapshot(collectionRef, (snapshot) => {
    students.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
  });
  console.log(students.value)
});

const createStudent = async () => {
  const dataObj = {
    lname: lname.value,
    fname: fname.value,
    gender: gender.value,
    grade: grade.value,
  };

  try {
    const docRef = await addDoc(collectionRef, dataObj);
    console.log('Document was Created with ID', docRef.id);
  } catch (error) {
    console.error('Error creating document:', error.message);
  }

  // Clear input fields after adding a student
  lname.value = '';
  fname.value = '';
  gender.value = '';
  sex.value = '';
  grade.value = '';
};

const filteredStudents = computed(() => {
  return students.value.filter(student => {
    // Check if any of the fields (lname, fname, gender) contain the search query
    return (
      student.lname.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
      student.fname.toLowerCase().includes(searchQuery.value.toLowerCase()) ||
      student.gender.toLowerCase().includes(searchQuery.value.toLowerCase())
    );
  });
});
const deleteStudent = async (id) => {
  try {
    await deleteDoc(doc(collectionRef, id));
    console.log('Document successfully deleted!');
  } catch (error) {
    console.error('Error deleting document:', error.message);
  }
};

</script>

