  margin-top: 150px;
  margin-left: 300px;
}
table {
  margin-top: 20px;
  margin-left: 200px;
  border-collapse: separate;
  border-spacing: 5px 10px; /* Adjust these values to set the horizontal and vertical spacing */
}
th{
  background-color: rgb(92, 92, 236);
}
th, td {
  border: solid #444 1px;
  padding: 10px; /* Optional: Add padding to th and td for better spacing */
}
.low-grade {
  background-color: rgb(199, 130, 130);
}

.high-grade {
  /* Add any styles for grades 75 and above */
background-color: rgb(13, 226, 120);
}
